body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0;
}

.app {
  text-align: center;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

#phaser-game {
  border: 2px solid #000;
  background-color: #fff;
}

.button-panel {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.control-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid #000;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.control-button:hover {
  background-color: #0056b3;
}
